"use client";

import { useRouter } from "next/navigation";
import { X } from "lucide-react";

const PaymentModal = () => {
  const router = useRouter();

  return (
    <div className="fixed inset-0 z-50 flex items-center justify-center bg-black/40 px-4">
      <div className="relative w-full max-w-xl rounded-2xl bg-white p-8 shadow-lg">
        {/* Close Button */}
        <button
          onClick={() => router.push("/")}
          className="absolute right-5 top-5 text-gray-500 hover:text-black"
        >
          <X size={22} />
        </button>

        {/* Success Icon */}
        <div className="flex justify-center">
          <div className="flex h-14 w-14 items-center justify-center rounded-full border-2 border-green-500">
            <svg
              width="26"
              height="26"
              viewBox="0 0 24 24"
              fill="none"
              stroke="#22c55e"
              strokeWidth="3"
              strokeLinecap="round"
              strokeLinejoin="round"
            >
              <polyline points="20 6 9 17 4 12" />
            </svg>
          </div>
        </div>

        {/* Title */}
        <h2 className="mt-6 text-center text-2xl font-semibold">
          Thanks for your order!
        </h2>

        <p className="mt-2 text-center text-sm text-gray-600">
          Your payment was successful and your order is been processed.
        </p>

        {/* Divider */}
        <div className="my-6 border-t" />

        {/* Order Details */}
        <div className="space-y-4 text-sm">
          <DetailRow
            label="Transaction date"
            value="Friday, 14th November 2025"
          />
          <DetailRow
            label="Payment method"
            value="Mastercard ending with 0264"
          />
          <DetailRow
            label="Delivery method"
            value="Pickup station - FLEZTA pickup station"
          />
          <DetailRow label="Amount paid" value="$1,905.99" bold />
        </div>

        {/* Buttons */}
        <div className="mt-8 grid grid-cols-2 gap-4">
          <button
            onClick={() => router.push("/orders")}
            className="rounded-full bg-[#063A2B] py-3 text-sm font-medium text-white hover:opacity-90"
          >
            Track Your Order
          </button>

          <button
            onClick={() => router.push("/")}
            className="rounded-full border border-[#CFF5E6] bg-[#E6FFF7] py-3 text-sm font-medium text-[#063A2B]"
          >
            Back To Home
          </button>
        </div>

        {/* Download Receipt */}
        <button
          onClick={() => alert("Download receipt")}
          className="mt-5 w-full rounded-full border border-orange-400 bg-orange-50 py-3 text-sm font-medium text-orange-500 hover:bg-orange-100"
        >
          Download Receipt
        </button>
      </div>
    </div>
  );
}

export default PaymentModal;

/* ---------------------------------- */
/* Reusable Detail Row Component */
/* ---------------------------------- */
function DetailRow({
  label,
  value,
  bold = false,
}: {
  label: string;
  value: string;
  bold?: boolean;
}) {
  return (
    <div className="flex justify-between">
      <span className="text-gray-700">{label}</span>
      <span className={bold ? "font-semibold text-black" : "text-gray-600"}>
        {value}
      </span>
    </div>
  );
}
